@import './common/_variables.scss';
body#tinymce {
  font-size: 12px;
  font-family: "Gotham A","Gotham B",sans-serif;
  h1 {
    font-size: 3.5em;
  }

  h2 {
  	font-size: 3.3em;
  }

  h3 {
  	font-size: 1em;
  }

  h4 {
  	font-size: 1.2em;
  	padding-top: 15px;
  	margin: 0 0 5px 0;
  }
  p {
    font-size: 1rem;
    color: $grey;
    line-height: 1.5em;
  }
  .preamble {
  	font-size: 1.3rem;
  	line-height: 1.4em;
  }
  blockquote {
    width: 50%;
    color: $primary-color;
    display: inline-block;
    float: left;
    margin: 0;
    padding: 5px 25px 5px 35px;
  }
  blockquote p {
    color: $primary-color;
  	font-size: 2rem;
  	font-weight: bold;
  	padding: 5px 20px 25px;
  	line-height: 2rem;
  }
  blockquote + p {
    width: 50%;
    display: inline-block;
    margin-left: 0;
    margin-right: 0;
  }
}
